* {
    box-sizing: border-box;
}

body {
    margin: 0;
    background-color: #333;
}

.nav {
    background-color: #333;
    color: white;
    display: flex;
    justify-content: space-between;
    height: 5rem;
    align-items: stretch;
    gap: 2rem;
    padding: 3rem 7rem;
}

.site-title {
    font-size: 3rem;
}

.nav ul {
    padding: 0;
    margin: 0;
    list-style: none;
    display: flex;
    gap: 1rem;
    font-size: 1.5rem;
}

.nav a {
    height: 100%;
    display: flex;
    align-items: center;
    padding: .25rem;
    color: inherit;
    text-decoration: none;
}

.nav li.active {
    background-color: #555;
}

.nav li:hover {
    background-color: #777;
}

.mainBody {
    display: flex;
    justify-content: center;
    align-items: center;
    color:white;
    flex-direction: column;
}

.textBody {
    padding-left:25rem;
    padding-right:25rem;
    padding-bottom: 2rem;
    font-size:larger;
}

.form-inputs p{
    color:red;
}
